import './Latitude.css';

const Latitude = () => {
    return (
    <tr>
        <td>Широта</td>
        <td><input placeholder = "Градусы" maxLength="2" name = "phi_grad" size ="4"></input></td><td>°</td>
        <td><input placeholder = "Минуты" maxLength="2" name = "phi_min" size ="4"></input></td><td>'</td>
        <td><input placeholder = "Секунды" maxLength="2" name = "phi_sec" size ="4"></input></td><td>''</td>
        <td>
        <select name = "phi_side" size = "1">
        <option value = "0" selected>с.ш.</option>
        <option value = "1">ю.ш.</option>
        </select>
        </td>
    </tr>
    );
}

export default Latitude