import './Scale.css';

const Scale = () => {
    return (
    <tr>
        <td>Масштаб</td>
        <td colSpan = "6">
            <select name = "scales" size = "1">
                <option value = "0" selected>1 : 50 000</option>
                <option value = "1">1 : 100 000</option>
                <option value = "2">1 : 200 000</option>
                <option value = "3">1 : 500 000</option>
                <option value = "4">1 : 1 000 000</option>
            </select>
        </td>
    </tr>
    );
}

export default Scale