import Calculate from './Calculate/Calculate';
import './Container.css';
import Latitude from './Latitude/Latitude';
import Longitude from './Longitude/Longitude';
import Scale from './Scale/Scale';

const Content = () => {
    return (
        <form name ="Container">
            <table>
                <tbody>
                    <Latitude />
                    <Longitude />
                    <Scale />
                    <Calculate />
                </tbody>
            </table>
        </form>
    );
}

export default Content;