import './Longitude.css';

const Longitude = () => {
    return (
    <tr>
        <td>Долгота</td>
        <td><input placeholder = "Градусы" maxLength="2" name = "lambda_grad" size ="4"></input></td><td>°</td>
        <td><input placeholder = "Минуты" maxLength="2" name = "lambda_min" size ="4"></input></td><td>'</td>
        <td><input placeholder = "Секунды" maxLength="2" name = "lambda_sec" size ="4"></input></td><td>''</td>
        <td>
        <select name = "phi_side" size = "1">
        <option value = "0" selected>в.д.</option>
        <option value = "1">з.д.</option>
        </select>
        </td>
    </tr>
    );
}

export default Longitude